<template>
<!-- 礼堂 -->
    <div class="guide">
        <div class="cat"><img :src=cat_url alt=""></div>
        <div class="left">
            <h3>{{this.message[0].title}}</h3>
            <h4>{{this.message[0].describe}}</h4>
        </div>
        <div class="right"><img :src="this.message[1].src" alt=""></div>
        <!-- <li v-for="(item,index) in message" :key="index">
            <a href="#">
                <h3>{{item.title}}</h3>
                <h4>{{item.describe}}</h4>
                <img :src="item.src" alt="">
            </a>
        </li> -->
    </div>
</template>

<script>
export default {
    name:'homeGuide',
    data(){
        return{
            message:[
                {title:'Hall',describe:'開揚明亮、舒適溫暖的環境及空間，與愛寵共渡最後的懷緬時光'},
                {src:'./static/images/cat_hall.webp'}
            ],
            cat_url:'./static/images/index_cat.svg'
        }
    }
}
</script>

<style scoped>
    /* 指导 */
.guide{
	display: flex;
	justify-content: space-between;
	background: #fff;
	padding: 0 .15rem ;
}
.guide div{
    width: 1.7rem;
    overflow: hidden;
	/* margin-bottom: .1rem; */
}
.guide .left{
    padding: 0.1rem;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content:center;
}
.guide div a{
	display: block;
	padding: .075rem;
}
.guide div h3{
	font-size: .16rem;
	font-weight: bold;
}
.guide div h4{
	font-size: .12rem;
	color: #7f7f7f;
	font-weight: normal;
}
.guide div img{
    border-radius: 10%;
	width: 1.6rem;
	height: 1.6rem;
}
.guide .cat{
    position: absolute;
    width: 1rem;
    height: 1rem;
    left: 50vw;
    transform: translate(-40%,100%);
}
.guide .cat img{
    width: 1rem;
    height: 1rem;
}
</style>


