<template>
  <div class="content">
    <!-- 内容区域 -->
    <div class="nav">
      <div class="background">
        <svg class="icon svg-icon" aria-hidden="true">
          <use xlink:href="#icon-shequ"></use>
        </svg>
        <p>{{message.header.title}}</p>
        <img :src="message.header.backgroundImgSrc" alt="" />
      </div>
      <div class="list-wrappers">
        <!-- 导航表格区域 -->
        <ul class="list">
          <li v-for="(item,index) in message.navTab" :key="index">
            <!-- <img :src="item.imgSrc" alt="" /> -->
            <svg class="icon svg-icon" aria-hidden="true">
              <use :xlink:href=item.href></use>
            </svg>
            <span>{{item.title}}</span>
            <p>{{item.describe}}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "goodsContent_Top",
  data(){
    return{
      message:{
          header:{
            title:'pet community',
            imgSrc:'./static/images/shopping-logo.png',
            backgroundImgSrc:'./static/images/shopping-background.jpg'
          },
          navTab:[
            {title:'宠物训练',describe:'pet train',href:'#icon-chongwuxunlian'},
            {title:'宠物饮食',describe:'pet eating',href:'#icon-red-chongwugoupen'},
            {title:'宠物',describe:'pet',href:'#icon-shengdanjieicon-'},
            {title:'宠物',describe:'pet',href:'#icon-chongwu'},

          ]
      }
    }
  }
};
</script>

<style scoped>
    /* content 内容区域 */
.content .nav{
    position: relative;
    width: 3.75rem;
    height: 1.5rem;
    padding-top: .725rem;
}
.content .nav .background{
    position: absolute;
    top: 0px;
    z-index: -1;
}
.content .nav .background svg{
  position: absolute;
  width: .65rem;
  height: .45rem;
  top: .3rem;
  left: .06rem;
  z-index: 2;
}
.content .nav .background img:nth-child(1){
    position: absolute;
    width: .65rem;
    top: .3rem;
    left: .06rem;
    z-index: 2;
}
.content .nav .background p{
    position: absolute;
    height: .22rem;
    line-height: .22rem;
    font-size: .15rem;
    left: .73rem;
    top: .4rem;
    color: #fff;
}
.content .nav .background img:nth-child(3){
    width: 3.75rem;
}

    /* 列表区域 */
.content .nav .list-wrappers{
    box-sizing: border-box;
    margin: 0 .1rem;

}

.content .nav .list{

    background: #fff;
    border-radius: .16rem;
    height: 1.4rem;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;   
}
.content .nav .list li{
    display: flex;
    width: 25%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.content .nav li svg{
    width: .55rem;
    height: .55rem;
}
.content .nav li span{
    font-size: .14rem;
    color: rgb(51, 51, 51);
}
.content .nav li p{
    font-size: .12rem;
    color: rgb(153, 153, 153);
}

</style>


