<template>
    <!-- banner区域 -->
        <div class="banner">
            <div class="mask">
                <i class="iconfont">&#xe611;</i> Group - Undefined
            </div>
            <van-swipe class="my-swipe" :autoplay="2000" indicator-color="white">
                <van-swipe-item v-for="(imgSrc,index) in swipeMsg" :key="index">
                    <img :src="imgSrc">
                </van-swipe-item>
            </van-swipe>
        </div>
</template>

<script>
import { Swipe, SwipeItem } from 'vant'
export default {
    name:'homeBanner',
    components:{Swipe, SwipeItem},
    data(){
        return{
            swipeMsg:[
                './static/images/pet1.webp',
                './static/images/pet2.webp',

            ]
        }
    }
}
</script>

<style scoped>
/* banner区域 : 轮播图*/
.banner{
    position: relative;
}
.banner .mask{
    position: absolute;
    width: 100vw;
    height: 100%;
    font-size: .2rem;
    text-align: center;
    line-height: 1.9rem;
    color: #fff;
    z-index: 1;
}
.banner .mask .iconfont{
    font-size: .2rem;
}
.my-swipe .van-swipe-item {
color: #fff;
font-size: 20px;
line-height: 150px;
text-align: center;
background-color: #39a9ed;
}
.van-swipe-item img{
	width: 100vw;
}


</style>


