<template>
    <!-- slogan -->
    <div class="newPeople">
        <h3><span>{{newPeople.title}}</span></h3>
        <div class="content">
			<router-link class="left" to='/Funeral'>
                <h4>{{newPeople.leftContent.title}}</h4>
                <div class="img" :style="{backgroundImage:`url(${newPeople.leftContent.imgSrc})`}"></div>
        	</router-link>

            <div class="right">
				<router-link  to='/Funeral'>
					<div v-for="(item,index) in newPeople.rightContent" :key="index" 
					:style="{background:` url(${item.imgSrc}) no-repeat right bottom/1rem 1rem`}">
						<h4>{{item.title}}</h4>
                    	<p>{{item.describe}}</p>
					</div>
				</router-link>
                <div class="btn">
					<van-button to='/Funeral' plain size="small" round >Learn more ...</van-button>
				</div>
            </div>
        </div>
    </div>
</template>

<script>
import { Button } from 'vant';

export default {
    name:'homeNewUser',
	components:{Button},
	data(){
		return{
			newPeople:{
				title:'Pet Go',
				leftContent:{
					title:'你的寵物值得一次體面的告別',
					imgSrc:'./static/images/dog.svg'
				},
				rightContent:[
					{
						title:'slogan',
						describe:'我們為每一位悲痛的寵物家長和客戶提供最高標準而感到自豪。為你提供各種各樣的寵物善後服務',
						backgroundColor:'#fff'
					}
				]
			}
		}
	}
}
</script>

<style scoped>
    /* 新人专享礼区域 */
.newPeople{
	background: #fff;
	margin: .1rem 0 0 0;
}
.newPeople h3{
	height: .45rem;
	line-height: .45rem;
	font-size: .16rem;
	text-align: center;
	font-weight: normal;
}
.newPeople h3 span{
	position: relative;
}
.newPeople h3 span::before , .newPeople h3 span::after{
	content: '';
	width: .12rem;
	height: .02rem;
	position: absolute;
	top: 50%;
	margin-top:  -.01rem;
	background: #333;
}
.newPeople h3 span::before{
	left: -0.2rem;
}
.newPeople h3 span::after{
	right: -0.2rem;
}
.newPeople .content{
	display: flex;
	justify-content: space-between;
	padding: 0 .15rem .15rem;
}
.newPeople .left{
	width: 1.67rem;
	height: 2.17rem;
	border-radius: 10%;
	background: #f9e9cf;
}
.newPeople .left h4{
	font-size: .16rem;
	font-weight: normal;
	padding: 0.15rem;/* 上右下左 */
}
.newPeople .left .img{
	width: 1.29rem;
	height: 1.28rem;
	margin: 0 auto;
	/* 以背景形式的图片适配方法 */
	/* background-image: url(../assets/images/img_01@1x.png); */
	/* background-image: -webkit-image-set(url(../assets/images/img_01@1x.png) 2x, url(../assets/images/img_01@2x.png) 3x); */
	background-size: cover;
}
.newPeople .right {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}
	/* 对右边区域的文字进行定位、样式修改 */
.newPeople .right a{
	width: 1.67rem;
	height: 1.07rem;
	border-radius: .02rem;
	box-sizing: border-box;
	padding: .1rem;
	position: relative;
}

.newPeople .right  h4{
	font-size: .16rem;
	font-weight: bold;
	margin-bottom: .025rem;
}
.newPeople .right  p{
	color: #7f7f7f;
	font-size: .12rem;
}


.newPeople .right a:nth-child(2) p{
	font-size: .11rem;
	color: #fff;
	text-align: center;
	line-height: .15rem;

	background: #cbb693;
	display: inline-block;
	border-radius: .02rem;
	padding: 0 .05rem
}

</style>


