<template>
<!-- 分类 -->
    <div class="content clearfix">
        <div class="cateLeft">
            <van-sidebar v-model="activeKey">
                <van-sidebar-item v-for="(item,index) in message.sortTitle" :key="index" :title=item.title />
            </van-sidebar>
        </div>

        <!-- 分类-left -->
        <!-- <ul class="cateLeft">
            <li v-for="(item,index) in message.sortTitle" :key="index" :class="item.active?'active':''">{{item.title}}</li>
        </ul> -->
        <!-- 分类-right -->
        <div class="cateRight">
            <!-- 轮播图 -->
            <div class="banner">
                <van-swipe class="my-swipe" :autoplay="2000" indicator-color="white">
                    <van-swipe-item  v-for="(item,index) in message.swiper" :key="index">
                        <img :src="item.src">
                    </van-swipe-item>
                </van-swipe>
            </div>
            <!-- 活动推荐 -->
            <ul class="cateRight-list">
                <li @click="openDialog(item)" v-for="(item,index) in message.sortList" :key="index">
                    <svg class="icon svg-icon" aria-hidden="true" >
                        <use :xlink:href=item.href></use>
                    </svg>
                    <!-- <img :src="item.src" alt=""> -->
                    <span>{{item.title}}</span>
                </li>
            </ul>
        </div>
        <div class="dialog">
            <van-dialog v-model="show" :title="this.dialogTitle" show-cancel-button>
                <svg class="icon svg-icon" aria-hidden="true" >
                    <use :xlink:href=this.dialogIcon></use>
                </svg>
                <!-- <img src="https://img01.yzcdn.cn/vant/apple-3.jpg" /> -->
            </van-dialog>    
        </div>

    </div>
</template>

<script>
import Vue from 'vue';
import { Sidebar, SidebarItem } from 'vant';
import { Dialog } from 'vant';


Vue.use(Dialog);
Vue.use(Sidebar);
Vue.use(SidebarItem);
export default {
    name:'sortContent',
    data(){
        return{
            dialogTitle:'',
            dialogIcon:'',
            activeKey:1,
            message:{ 
                sortTitle:[
                    {title:'寵物葬禮',active:true},
                    {title:'骨灰寄存',active:false},
                    {title:'毛髮修剪',active:false},
                    {title:'遺體洗護',active:false},
                    {title:'攝影紀念',active:false},
                    {title:'寵物配飾',active:false},
                    {title:'棺材款式',active:false},
                    {title:'花圈',active:false},
                    {title:'寵物項圈',active:false}
                ],
                swiper:[
                    {src:'./static/images/pet1.webp',},
                    {src:'./static/images/pet2.webp',}
                ],
                sortList:[
                    {title:'棺材',href:'#icon-shengdanjieicon-'},
                    {title:'铃铛',href:'#icon-chongwu'},
                    {title:'宠物洗护',href:'#icon-red-chongwuxihu'},
                    {title:'宠物修剪',href:'#icon-red-chongwuxiujian'},
                    {title:'宠物乐园',href:'#icon-chongwuleyuan'},
                    {title:'宠物摄影',href:'#icon-chongwusheying'},
                    {title:'宠物配饰',href:'#icon-chongwupeishi'},
                ]
            },
            show:false
            
        }
    },
    methods: {
        openDialog(item){
            this.dialogIcon=item.href
            this.dialogTitle=item.title
            this.show=true
        }
    }

}
</script>

<style scoped>

/* 内容区域 */
    /* 分类 */
.content{
    /* position: absolute; */
    box-sizing: border-box;
    background-color: #f7f8fa;
    height: calc(100vh - .45rem);
    /* padding: .44rem 0 .48rem  0;
    top: 0; */
}
.content .cateLeft{
    padding:.2rem 0;
    float: left;
    width: .81rem;
    border-right: 1px solid #d9d9d9;
}
.cateLeft li{
    box-sizing: border-box;
    width: 100%;
    height: .25rem;
    margin-top: .2rem;
    text-align: center;
    line-height: .25rem;
    font-size: .14rem;
}
.cateLeft li.active{
    margin-top: 0%;
    color: #AB2B2B;
    border-left: .05rem solid #ab2b2b;
}
    /* 右边内容 */
.content .cateRight{
    height: 100%;
    box-sizing: border-box;
    color: #666;
    margin-left: .81rem;
    padding: .15rem .15rem .1rem;
}

.cateRight .banner{
    width: 2.64rem;
    height: .96rem;
    margin-bottom: .16rem;
    overflow: hidden;
	position: relative;
}
.cateRight .banner .wrap{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
}
.cateRight .banner img{
    width: 2.64rem;
}

.banner .circle{
	position: absolute;
	bottom: .08rem;
	width: 100%;
	display: flex;
	justify-content: center;
}
.banner .circle span{
	width: .2rem;
	height: .02rem;
	background: rgba(255, 255, 255, .5);
	margin: 0 .05rem;
}
.banner .circle span.active{
	background: rgba(255, 255, 255, 1);
}

/* 右边底部 */
.cateRight-list{
    width: 2.64rem;
}
.cateRight-list li{
    width: 0.72rem;
    height: 1.08rem;
    float: left;
    margin-right: .17rem;
    display: flex;
    flex-direction: column;
}
.cateRight-list li span{
    text-align: center;
    color: #000;
    margin-top: .03rem;
}
.cateRight-list li:nth-child(3n){
    margin-right: 0;
}


.cateRight-list svg{
    width: 0.72rem;
    height: 0.72rem;
}
.dialog svg{
    font-size: 1.5rem;
    transform: translateX(50%);
}

</style>


