import Vue from "vue";
import Router from "vue-router";

// 引入路由组件
import Home from "../pages/Home.vue"  //主页
import Shopping from "../pages/Shopping.vue"; //Community
import Search from "../pages/Search.vue"; // 搜索界面
import Funeral from "../pages/Funeral.vue"; // 葬礼界面
import CateList from "../pages/CateList.vue";


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      component: Home,
    },{
      path: "/Home",
      name: "Home",
      component: Home,
    },{
      path: "/Community",
      name: "Community",
      component: Shopping,
    },{
      path: "/Search",
      name: "Search",
      component: Search,
    },{
      path: "/Funeral",
      name: "Funeral",
      component: Funeral,
    },
    ,{
      path: "/CateList",
      name: "CateList",
      component: CateList,
    }
  ],
});



