<template>
<div>
    <!-- nav区域 -->
    <nav class="nav">
        <van-nav-bar
        title="善终葬礼"
        left-text="返回"
        left-arrow
        @click-left="backHome()"
        />

    </nav>
</div>
</template>

<script>

import Vue from 'vue';
import { NavBar } from 'vant';

Vue.use(NavBar);

export default {
    name:'funeralNav',
    methods: {
        backHome(){
            console.log(this.$router.push('\Home'))
        }
    }
    
}
</script>

<style scoped>
/* <!-- nav区域 --> */
.nav{
	background: #fff;
}
</style>


