<template>
<!-- 礼堂 -->
    <div class="guide">
        <div class="right">
            <img :src="this.message[1].src" alt="">
        </div>
        <div class="left">
            <h3>{{this.message[0].title}}</h3>
            <h4>{{this.message[0].describe}}</h4>
        </div>
    </div>
</template>

<script>
export default {
    name:'homeGuide',
    data(){
        return{
            message:[
                {title:'treat pet as people',describe:'鮮花、音樂，富尊嚴、具體面及個性化的彩虹告別禮'},
                {src:'./static/images/hall.jpeg'}
            ]
        }
    }
}
</script>

<style scoped>
    /* 指导 */
.guide{
	display: flex;
	justify-content: space-between;
	background: #fff;
	padding: 0 .15rem .15rem;
}
.guide div{
    width: 1.7rem;
    
    overflow: hidden;
	/* margin-bottom: .1rem; */
}
.guide .left{
    /* padding: 0.1rem; */
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content:center;
}
.guide div a{
	display: block;
	padding: .075rem;
}
.guide div h3{
	font-size: .16rem;
	font-weight: bold;
}
.guide div h4{
	font-size: .12rem;
	color: #7f7f7f;
	font-weight: normal;
}
.guide div img{
    border-radius: 10%;
	width: 1.7rem;
	height: 1.7rem;
}
</style>


