<template>
    <div class="navItem">
        <router-link :to="href">
           <i class="iconfont">{{this.icon}}</i>
            <span>{{title}}</span>
        </router-link>
    </div>
</template>

<script>
export default {
    name:'NavItem',
    props:['navMessage'],
    data(){
        return{
            href:'',
            title:'',
            icon:''
        }
    },
    created(){
        let {href,title,icon} = this.navMessage
        this.href = href
        this.title = title
        this.icon = icon
    }

}
</script>

<style scoped>
.navItem i{
    font-size: 0.25rem;
}
.navItem span{
    font-size: 0.12rem;
}
</style>


